<template>
  <v-app>
    <v-app-bar
        fixed
        color="white"
        elevation="1"
    >

      <!--Back button-->
      <v-app-bar-nav-icon @click="profile()">
        <i class="fa fa-arrow-left fa-2x" style="color:#000000;" aria-hidden="true"></i>
      </v-app-bar-nav-icon>

      <!-- Page title -->
      <v-spacer></v-spacer>
      <v-toolbar-title id="Title">Edit Profile</v-toolbar-title>
      <v-spacer></v-spacer>

    </v-app-bar>
    
    <!--Page-->
    <v-main id="main">
      <v-container>
        <div class="content">

          <div v-if="tab == 'update'">
            <!--Update Success Message-->
            {{output}}

            <!--User Avatar-->
            <image-input class="avatar" @input="changeAvatar($event)">
              <div slot="activator">
                <v-avatar size="150px" v-ripple >
                  <img :src="userData.avatar">
                </v-avatar>
              </div>
            </image-input>

            <!-- <v-slide-x-transition>
              <div v-if="saved == false">
                <v-btn class="primary" @click="uploadImage" :loading="saving">Save Avatar</v-btn>
              </div>
            </v-slide-x-transition> -->
            
            <form @submit.prevent="updateProfile()" class="settingsForm">
              <div class="field" >
                <label>Username</label>
                <input type="text" name="username" v-model="username">
              </div>

              <div class="field" >
                <label>Email</label>
                <input type="email" name="email" v-model="email" v-validate="'required|email'">
              </div>

              <button type="submit" class="submitButton">Update profile</button>
              <button type="button" @click="tab = 'password', output = ''" class="submitButton">Change Password</button>
            </form>
          </div>

          <div v-if="tab == 'password'">
            <form @submit.prevent="changePassword()" class="settingsForm">
              <div class="field" >
                <label>Current Password</label>
                <input type="password" name="password" v-model="password" data-vv-as="current password" v-validate="'required'">
              </div>

              <div class="field" >
                <label>New Password</label>
                <input type="password" name="newPassword" v-model="newPassword" data-vv-as="new password" v-validate="'required'">
              </div>

              <div class="field" >
                <label>Confirm New Password</label>
                <input type="password" name="confirmPassword" v-model="confirmPassword" data-vv-as="confirm password" v-validate="'required|confirmed:newPassword'">
              </div>

              <button type="submit" class="submitButton">Update password</button>

              <button type="button" @click="tab = 'update'" class="submitButton">Go Back</button>
            </form>
          </div>
        </div>

      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import ImageInput from '../../components/ImageInput';
import axios from 'axios'
export default {
  name: 'App',

  components: {
    ImageInput: ImageInput
  },

  data: () => ({
    userData: null,
    avatar: null,
    username: "",
    email: "",
    password: '',
    newPassword: '',
    confirmPassword: '',
    saving: false,
    saved: false,
    tab: 'update',
    output: ''
  }),

  mounted() {
    axios.get('/public/profile/getUserData').then(response => (this.userData = response["data"]))
    this.initForm()
    // axios.get('/public/profile/getTags').then(response => (this.tags = response["data"]))
    // axios.get('/public/profile/getBadges').then(response => (this.badges = response["data"]))
  },

  methods: {
    fetchUserData()
    {
      return axios.get('/public/profile/getUserData').then(response => response["data"])
    },

    initForm(){
      this.fetchUserData().then((res) => {
        this.username = res.userName
        this.email =  res.emailAddress
      })
    },

    profile() {
      window.location.href = '/public/profile';
    },

    changeAvatar(array){
      this.userData.avatar = array.imageURL
      this.avatar = array.imageURL
    },

    uploadImage() {
      this.saving = true
      setTimeout(() => this.savedAvatar(), 1000)
    },

    savedAvatar() {
      this.saving = false
      this.saved = true
    },
    
    updateProfile(){
      this.userData.userName = this.username
      this.userData.emailAddress = this.email

      let currentObj = this;

      let rawData = JSON.stringify(this.userData)
      let formData = new FormData()
      formData.append('data', rawData)

      axios.post('/public/profile/updateProfile', formData).then(function (response) {currentObj.output = response.data;}).catch(function (error) {
         currentObj.output = error;
      });
    },

    changePassword(){

    }
  }
};
</script>

<style>
@import '../styles.css';
#main {
  background-image: url(leaves.png);
  background-color: rgba(255, 255, 255, 0.4);
  background-blend-mode: lighten;
  background-repeat: repeat;
  background-position: center;
}

.content{
  margin-top: 8rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.avatar{
  margin-bottom: 1rem;
}

/* .settingsForm {
  padding-bottom: 2rem;
} */

.field {
  display: block;
  margin-bottom: 1rem;
  /* margin-left: auto;
  margin-right: auto;
  padding: 20px 50px 10px 10px;
  border-bottom: 3px solid white;
  width: 150px; */
}

label{
  display: block;
}

input {
  background-color: transparent;
  border: 0.05rem solid black;
  border-radius: 0.35rem;
  color: black;
  outline: none;
}

.submitButton{
  display: block;
  background: #00251A;
  color: white;
  border-radius: 15px;  
  width: 100%;
  margin: 1.5rem 0 1.5rem 0;
  outline: none;
}

</style>